:host {
  display: block;
}

.inline-data-type-selector {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 0.75rem;
  background-color: var(--card-background-color, #ffffff);
  border-radius: 0.75rem;
  border: 1px solid var(--input-border-color, #cbd5e1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

// Header
.selector-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}

.header-label {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-color, #334155);
}

.header-actions {
  display: flex;
  gap: 0.375rem;
}

.action-btn {
  padding: 0.25rem 0.625rem;
  font-size: 0.75rem;
  font-weight: 500;
  background: transparent;
  border: 1px solid var(--input-border-color, #d1d5db);
  border-radius: 0.375rem;
  color: var(--text-color, #4b5563);
  cursor: pointer;
  transition: all 0.15s ease;

  &:hover {
    background-color: var(--surface-hover, #f3f4f6);
    border-color: var(--text-color-secondary, #9ca3af);
  }
}

// Data Type Grid
.data-type-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;

  @media (max-width: 400px) {
    grid-template-columns: 1fr;
  }
}

.data-type-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.25rem;
  padding: 0.75rem;
  background-color: var(--surface-hover, #f8fafc);
  border: 2px solid var(--input-border-color, #e2e8f0);
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.15s ease;
  text-align: left;
  position: relative;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);

  &:hover {
    background-color: var(--surface-ground, #f1f5f9);
    border-color: var(--color-primary-color, #3b82f6);
    box-shadow: 0 2px 4px rgba(var(--color-primary-rgb, 59, 130, 246), 0.15);
  }

  &.selected {
    background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    border-color: var(--color-primary-color, #3b82f6);
    box-shadow: 0 2px 4px rgba(var(--color-primary-rgb, 59, 130, 246), 0.2);

    .card-icon {
      color: var(--color-primary-color, #3b82f6);
    }
  }

  &.collecting {
    background-color: #fef3c7;
    border-color: #f59e0b;
    cursor: not-allowed;
    opacity: 0.9;
    box-shadow: 0 2px 4px rgba(245, 158, 11, 0.15);

    .card-icon {
      color: #d97706;
    }

    .card-description {
      color: #b45309;
      font-style: italic;
    }

    &:hover {
      background-color: #fef3c7;
      border-color: #f59e0b;
    }
  }
}

.card-checkbox {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;

  input[type='checkbox'] {
    width: 0.875rem;
    height: 0.875rem;
    accent-color: var(--color-primary-color, #3b82f6);
    cursor: pointer;
  }

  .collecting-spinner {
    font-size: 0.875rem;
    color: #f59e0b;
  }
}

.card-icon {
  font-size: 1.25rem;
  color: var(--color-primary-color, #3b82f6);
  margin-bottom: 0.25rem;
}

.card-label {
  font-size: 0.8125rem;
  font-weight: 600;
  color: var(--text-color, #1e293b);
}

.card-description {
  font-size: 0.6875rem;
  color: var(--text-color-secondary, #64748b);
  line-height: 1.3;
}

// Collecting Status
.collecting-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: #fef3c7;
  border-radius: 0.375rem;
  border: 1px solid #f59e0b;
  font-size: 0.75rem;
  color: #b45309;

  i {
    font-size: 0.875rem;
    color: #d97706;
  }
}

// Selection Summary
.selection-summary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
  border-radius: 0.375rem;
  border: 1px solid var(--color-primary-color, #3b82f6);
}

.summary-text {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--color-primary-color, #3b82f6);

  i {
    font-size: 1rem;
  }
}

::ng-deep .confirm-btn {
  background: var(--color-primary-color, #3b82f6);
  border: none;
  font-weight: 600;

  &:hover {
    filter: brightness(0.9);
  }
}
